const tips=
    [
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        ''
    ];

let index=getRandom();
function getRandom(){
    return Math.floor((Math.random()*tips.length));
}
export function getTip(){
    if(index==tips.length){
        index=0;
    }
    let current=tips[index];
    index+=1;
    return current;
}


